	module.exports = {"data":{"nimblRenderedHtml":{"html":"<h1 id=\"eliot\">Eliot</h1>\n<h2 id=\"life\">Life</h2>\n<p>Thomas Stearns Eliot was born in 1888 from an english family, he studied at Harvard and at the Sorbonne. When the World War I broke out he left Germany and went to Oxford.</p>\n<p>In 1921 he almost had a nervous breakdown and was treated in Margate and the in Lausanne (switzerland).</p>\n<p>In the late 20s and 30s he found in religion a way out of nihilism.</p>\n<p>In 1948 he won the Nobel Prize and was appointed the British Order of Merit.</p>\n<h2 id=\"works\">Works</h2>\n<p>His first work was <em>The Love Song of J.Alfred Prufrock</em>, published in 1917, his first successful work is <em><strong>The Waste Land</strong></em>, first drafted while in Lausanne and revised by Ezra Pound.</p>\n<p>After the sucess of The Waste Land he became the leading english modernist poet and left his job. This period culminated with the play <em>Murder in the Cathedral</em> (1935).</p>\n<p>After Word War II Eliot was mostly occupied with drama, criticism and reviewing. <mark>He treated religion and morality through sophisticated social comedies.</mark></p>\n<p>His literary criticism was as great as his writing, though not as appreciated due to the pitiless attacks on modern society.</p>\n<h2 id=\"the-waste-land\">The Waste Land</h2>\n<p>The Waste Land has been particularly important, because definitely abandoned the late Victorian models and greatly influenced the future poets.</p>\n<p>It expresses Eliot's <strong>disillusion</strong> with the world, the search for a new tradition, it's the culmination of his nihilism.</p>\n<p>The main topic the <strong>decay of western civilization</strong>, the poet sees only ruin and destruction around him. The fragmentation of the poem reflects the fragmentation of society.</p>\n<p>Using various images quotations from myths and legends Eliot describes the waste land whose ruler, the <strong>Fisher King</strong>, brought sterility to the land.<br>\n<mark>The order of myth is projected onto the chaos of modern life.</mark></p>\n<p>The poem lacks a logical continuity, it instead procedes through free association of thoughts (<strong>stream of consciousness</strong>).</p>\n<p>He uses the free verse, although there are some regularities and rhymes.</p>\n<h1 id=\"joyce\">Joyce</h1>\n<h2 id=\"life-2\">Life</h2>\n<p>Joyce was born in a middle-family, his father was a supporter of the Home Rule for Ireland, but after Parnell's death (the leader of the movement) he retired from politics. This made him the representative of the <strong>failures of Ireland</strong>.</p>\n<p>In 1902 Joyce self-imposed the exile, because life in Ireland was an obstacle to his artistic development. In Trieste he finished <em><strong>Dubliners</strong></em> and <em>A Portrait of the Atist as a Young Man</em>.</p>\n<p>At the outbreak of World War I Joyce moved to Zurich, where he began to write the <em><strong>Ulysses</strong></em> (in which he embraced the stream-of-consciousness technique). He published it in Paris, where he also wrote <em><strong>Finnegans Wake</strong></em>.</p>\n<h2 id=\"themes\">Themes</h2>\n<p>Although he left Dublin, the city remained the main topic of all his works. He chose exile to gain an emotional and intellectual detachment.</p>\n<p>Throughout Joyce's works we see <mark>a shift from the particular to the universal</mark> and from the lyrical style to the epic style.</p>\n<h2 id=\"dubliners\">Dubliners</h2>\n<p>Dubliners is a collection of 15 short stories, in each one the failure of self-realization of a Dubliner is examined.</p>\n<p>Joyce focuses on the idea of the <strong>paralysis of will</strong> that leads people to accept the limitations of their social context.</p>\n<p>Though it recreated in extreme detail the environment of Dublin, Joyce also employed a subtle symbolism to shine a different light on ordinary objects. He used the term <strong>epiphany</strong> do descrbe an object making someone realise their condition (e.g. Gretta hearing the song)</p>\n<p>In the last story, <em>The Dead</em>, the narrator's objectivity is replaced by lyrical intensity and a more compassionate view of the people of Dublin.<br>\nIn the end the protagonist, Gabriel, abandons his frustrations and regrets and the union of the living with the dead is symbolized by the snow.</p>\n<h1 id=\"virginia-woolf\">Virginia Woolf</h1>\n<h2 id=\"life-3\">Life</h2>\n<p>She was educated at home, her family and acquaintances were literates. She had been <strong>mentally instable</strong> for all her life, having various periods of depression and even attempting suicide.</p>\n<p>Her house in London was the centre for the <strong>Bloomsbury Group</strong>, an exclusive group of writers, they were anti-Victorians, sceptical about religion and left-wing in politics.<br>\nShe has been an activist for the rights of women.</p>\n<p>World War II was too much for her to bear and she drowned herself.</p>\n<h2 id=\"style\">Style</h2>\n<p>She employed the stream of consciousness technique, indirect narration and the <strong>interior monologue</strong>.</p>\n<p>Her use of the time reflects her modernist ideas of plot, character and language.<br>\nThe plot covers only short stretches of time, but they are expanded by the links that the mind makes. She expresses this through the ideas of the <strong>time of the clock</strong> and <strong>time of the mind</strong>.</p>\n<h2 id=\"mrs-dalloway\">Mrs Dalloway</h2>\n<p>Its the story of an <strong>ordinary day</strong> in the life of Clarissa Dalloway. Woolf rejects the idea of the eventful plot and instead focuses on the interaction between the character's thoughts and the external reality, to do so she makes great use of the interior monologue.</p>\n","frontmatter":{"title":"The modernists","pdfCompletoPath":null,"draft":false},"headings":[{"value":"<p>Eliot</p>\n","depth":1,"rawText":"Eliot"},{"value":"<p>Life</p>\n","depth":2,"rawText":"Life"},{"value":"<p>Works</p>\n","depth":2,"rawText":"Works"},{"value":"<p>The Waste Land</p>\n","depth":2,"rawText":"The Waste Land"},{"value":"<p>Joyce</p>\n","depth":1,"rawText":"Joyce"},{"value":"<p>Life</p>\n","depth":2,"rawText":"Life"},{"value":"<p>Themes</p>\n","depth":2,"rawText":"Themes"},{"value":"<p>Dubliners</p>\n","depth":2,"rawText":"Dubliners"},{"value":"<p>Virginia Woolf</p>\n","depth":1,"rawText":"Virginia Woolf"},{"value":"<p>Life</p>\n","depth":2,"rawText":"Life"},{"value":"<p>Style</p>\n","depth":2,"rawText":"Style"},{"value":"<p>Mrs Dalloway</p>\n","depth":2,"rawText":"Mrs Dalloway"}]}},"pathContext":{"category":"English literature","title":"The modernists"}}

